@mixin flex($direction, $align, $justify){
    display: flex;
    flex-direction: $direction;
    align-items: $align;
    justify-content: $justify;
}

@mixin responsive($screenWidth){
    @media(max-width: $screenWidth){
        @content;
    }
}
@mixin centerTotalFlex {
    display: flex;
    align-items: center;
    justify-content: center;
  }